define('qumla/helpers/to-ascii', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports.toAscii = toAscii;

	function toAscii(params) {
		var str = params;
		if (Ember['default'].$.isArray(params)) {
			str = params[0];
		}
		if (str) {
			return str.replace(/[őóöÖŐÓ]/g, "o").replace(/[üúűÜÚŰ]/g, "u").replace(/[áÁ]/g, "a").replace(/[éÉ]/g, "e").replace(/[Íí]/g, "i").replace(/[?! #'\"\s\\\/]/g, "-");
		} else {
			return 'unknown';
		}
	}

	exports['default'] = Ember['default'].Helper.helper(toAscii);

});